import React, {Component} from 'react';
import {Marker, Popup} from "react-leaflet";
import {Avatar, Card, Icon, Skeleton, Modal, message, Input} from "antd";
import PropTypes from 'prop-types'

const { Meta } = Card;

class PopupMarker extends Component {
    state = {
        editVisible: false,
        editLoading: false
    };

    static propTypes = {
        position: PropTypes.array.isRequired,
        title: PropTypes.string.isRequired,
        description: PropTypes.string.isRequired,
        time: PropTypes.string.isRequired
    };

    showMore = () => {
        let {position, title, description, time} = this.props;
        if(description) {
            Modal.info({
                title: '信息详情',
                content: (
                    <div>
                        <span>当前坐标类型 : {title}</span><br/>
                        <span>纬度 : {position[0]}</span><br/>
                        <span>经度 : {position[1]}</span><br/>
                        <span>描述信息 : {description}</span><br/>
                        <span>信息最后修改时间 : {time}</span>
                    </div>
                ),
                // onOk() {
                //     message.info('了解')
                // }
            });
        }
    };

    SaveMsg = () => {
        this.setState({
            editLoading: true,
        });
        setTimeout(() => {
            //TODO 保存该点
            message.success('保存成功');
            this.setState({
                editLoading: false,
                editVisible: false
            });
        }, 2000);
    };

    delMsg = () => {
        Modal.confirm({
            title: '你确定要删除吗',
            content: '删除后将不会显示这个坐标点',
            okText: '删除',
            okType: 'danger',
            cancelText: '取消',
            onOk() {
                setTimeout(()=>{
                    //TODO 删除该点
                    message.success('删除成功');
                },2000)
            },
            // onCancel() {
            //     console.log('取消删除')
            // }
        });
    };

    render() {
        let {position, title, description} = this.props;
        let {editVisible, editLoading} = this.state;

        return (
            <div>
                <Modal
                    title='编辑内容'
                    visible={editVisible}
                    confirmLoading={editLoading}
                    onOk={this.SaveMsg}
                    onCancel={()=>{this.setState({editLoading: false,editVisible: false})}}
                    okText="保存"
                    cancelText="取消"
                >
                    <Input.TextArea rows={4} defaultValue={description}/>
                </Modal>
                <Marker position={position}>
                    <Popup>
                        <Card
                            style={{width: 250}}
                            loading={false}
                            actions={[
                                <Icon type="ellipsis" key="ellipsis" onClick={this.showMore}/>,
                                <Icon type="edit" key="edit" onClick={()=>{this.setState({editVisible: true})}}/>,
                                <Icon type="delete" key="delete" onClick={this.delMsg}/>
                            ]}
                        >
                            <Skeleton loading={!description} avatar active>
                                <Meta
                                    avatar={
                                        <Avatar icon="user" src={require('../../../assets/avatar.jpg')} />
                                    }
                                    title={title}
                                    description={description}
                                />
                            </Skeleton>
                        </Card>
                    </Popup>
                </Marker>
            </div>
        );
    }
}

export default PopupMarker;